/*cloudy sunny day*/

body
{
  background: rgba(30, 144, 255, 0.7);
}
#container
{
  background-color:rgba(0,0,50,0.2);
}

.wrapper {
    width: 240px;
    height: 50px;
    background: #fff;
    border-radius: 20px;
    margin: 0 auto;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -125px;
}

.round {
    width: 100px;
    height: 100px;
    background-color: #fff;
    border-radius: 50%;
    position: absolute;
    margin-top: -50px;
    margin-left: 20px;
}

.round2 {
    margin-left: 100px;
    width: 120px;
    height: 120px;
    margin-left: 100px;
    margin-top: -70px;
}

.sun {
    z-index: -1;
    background-color: yellow;
    box-shadow: 0px 0px 100px 10px yellow;
    margin-left: 75px;
    animation: sunny 5s linear infinite alternate;
}
#returncontainer
{
  position:relative;
  margin:auto;
  width:100%;
  top:10%;
  text-align:center;

}
#returnbutton
{
  width:200px;
  height:50px;
  position:relative;
  color:black;
}

@keyframes sunny {
    0% {
        margin-top: -60px;
    }
    100% {
        margin-top: -100px;
    }
}
