body
{
  background-color:rgba(0, 100, 215, 1);
}

#container
{
  background-color:rgba(0,0,50,0.2);
  height:auto;
}

.wrapper {
    width: 100%;
    height: 50px;
    //background: #fff;
    border-radius: 20px;
    margin: auto;

    position: relative;
    top: 230px;
    left: 50%;
    margin-left: -125px;
}
.round {
    width: 100px;
    height: 100px;
    //background-color: #fff;
    border-radius: 50%;
    position: absolute;
    margin-top: -50px;
    margin-left: 0px;
}

#returncontainer
{
  position:relative;
  margin:auto;
  width:100%;
  top:10%;
  text-align:center;

}
#returnbutton
{
  width:200px;
  height:50px;
  position:relative;
  color:black;
}
.sun {
    z-index: -1;
    background-color: yellow;
    box-shadow: 0px 0px 100px 10px yellow;
    margin-left: 75px;
    top:50%;
    animation: sunny 5s linear infinite alternate;
}


@keyframes sunny {
    0% {
        margin-top: -10px;
    }
    100% {
        margin-top: -0px;
    }
}
